import React from "react";
import {downloadAllFiles} from "../aws/aws-service";
import {RootState} from "../redux/actions";
import {connect, ConnectedProps} from "react-redux";
import ButtonWithDescription from "./button-with-description";
import ReactLoading from "react-loading";

export const mapState = (state: RootState) => {
    return {
        currentIteration: state.currentIteration,
        currentProject: state.currentProject
    };
};

export const mapDispatchToProps = {};

const connector = connect(mapState, mapDispatchToProps);

type PropsFromRedux = ConnectedProps<typeof connector>;

type Props = PropsFromRedux & {};

type State = {
    loadingStatus: string;
}

const Loading = "Loading";

type LoadingContainerProps = {
    status: string;
}

class LoadingContainer extends React.Component<LoadingContainerProps, {}> {

    constructor(props: LoadingContainerProps) {
        super(props);
    }

    render() {
        return (
          <div>
              { this.props.status.startsWith(Loading) ? <ReactLoading type={"spin"} color={"red"}/> : <p> { this.props.status } </p> }
          </div>
        );
    }
}

export default connector(class DownloadZip extends React.Component<Props, State> {

    constructor(props: Props) {
        super(props);
        this.state = {
          loadingStatus: 'Have not started a download yet...',
        };
    }

    async download(iteration: number) {
        try {
            this.setState({loadingStatus: 'Loading the log files from the cloud...'});
            const zipData = await downloadAllFiles(this.props.currentProject.Name, iteration);
            if ('s3Key' in zipData && 'localFileName' in zipData) {
                // https://davidwalsh.name/javascript-download
                const a = document.createElement("a");
                a.style.display = "none";
                document.body.appendChild(a);

                a.href = zipData.s3Key;

                // Use download attribute to set set desired file name
                a.setAttribute("download", zipData.localFileName);

                // Trigger the download by simulating click
                a.click();

                // Cleanup
                window.URL.revokeObjectURL(a.href);
                document.body.removeChild(a);
            } else {
                alert('Error while downloading files: ' + JSON.stringify(zipData));
            }
            this.setState({loadingStatus: `Done! Your downloaded file name is: ${zipData.localFileName}`});
        } catch (e){
            this.setState({loadingStatus: `An error occurred during the download: ${e.toString()}`});
        }
    }

    render() {
        return (
            <div>
                <LoadingContainer status={this.state.loadingStatus} />
                <ButtonWithDescription
                    buttonTitle={'Download All 0th Logs'}
                    description={'Downloads all logs in iteration 0. Iteration 0 is meant for use in collecting base line data and not meant for collecting information on student\'s hits.'}
                    buttonClass={'safe'}
                    onClick={() => this.download(0)}
                    display={true}
                />
                <ButtonWithDescription
                    buttonTitle={`Download All Logs for ${this.props.currentProject.Name} project, iteration ${this.props.currentIteration + 1}.`}
                    description={'Downloads all logs for the currently selected project and iteration. This is meant for collecting logs for student\'s hits following a successful HIT deployment to MTurk.'}
                    buttonClass={'safe'}
                    onClick={() => this.download(this.props.currentIteration)}
                    display={true}
                />
            </div>
        );
    }
});